.intro {
  margin: 0 auto;
  max-width: 1184px;
  padding: 112px 0 184px; }
  .intro__content {
    font-size: 1.25rem;
    font-family: Poppins;
    font-style: normal;
    font-weight: 300;
    line-height: 2.25rem;
    margin-bottom: 3.75rem;
    max-width: 780px; }
  .intro__img {
    border-radius: 50% 50% 50% 0;
    height: 120px;
    width: 120px; }
  .intro__link {
    color: #0593FB;
    font-weight: 500; }
  .intro__mail {
    border-bottom: 1px solid #0593FB;
    color: #0593FB;
    font-weight: 500;
    font-size: 1.25rem;
    text-decoration: none;
    padding-bottom: 8px; }
  .intro__title {
    font-family: Poppins;
    line-height: 3.5rem;
    margin: 2rem 0 1rem;
    max-width: 780px;
    font-size: 2.5rem;
    font-style: normal;
    font-weight: 600; }

.skills {
  max-width: 1184px;
  margin: 0 auto; }
  .skills__title {
    font-size: 2.25rem;
    font-style: normal;
    font-weight: 600;
    line-height: 3rem;
    padding: 1rem 0;
    border-bottom: 2px solid #52606D; }

.listed-skills {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 8rem 0 8rem; }
  .listed-skills__list {
    column-gap: 1.5rem;
    margin-top: calc(1.5rem + 2px);
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    list-style-type: none; }
    .listed-skills__list-element {
      min-height: 40px;
      padding: 11px 0 11px 30px;
      position: relative; }
      .listed-skills__list-element::before {
        color: #1f831f;
        content: '\f058';
        font-family: "Font Awesome 5 free";
        position: absolute;
        top: 10px;
        left: 0; }
        .listed-skills--violet .listed-skills__list-element::before {
          color: #653CAD; }
        .listed-skills--blue .listed-skills__list-element::before {
          color: #3737ce; }
  .listed-skills__title {
    font-family: Poppins;
    font-size: 2rem;
    font-style: normal;
    font-weight: 600;
    line-height: 2.75rem;
    margin: 0; }
    .listed-skills__title::before {
      content: '';
      background: #1f831f;
      border-radius: 1px;
      display: block;
      height: 2px;
      margin-bottom: 1.5rem;
      width: 80px; }
      .listed-skills--violet .listed-skills__title::before {
        background-color: #653CAD; }
      .listed-skills--blue .listed-skills__title::before {
        background-color: #3737ce; }

html {
  box-sizing: border-box;
  font-size: 16px;
  scroll-behavior: smooth; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background: #171F3F;
  border: 16px solid #171F3F;
  color: #52606D;
  font-family: "Poppins", sans-serif; }

.main {
  background-color: #fff; }

.project__img {
  max-width: 48px; }
